<!-- 中奖提示传 -->
<template>

	<view class="xh-winning-window-home" :style="{display: isShow?'block':'none'}">

		<!-- toast 背景 -->
		<image class="xwwh-dialog-bg animated  bounceInDown" src="../../../static/images/dialog_bg.png"></image>
		<!-- toast header -->
		<image v-if="prizeratetype !== 2" class="xwwh-dialog-bg-header-25 animated  bounce"
			src="../../../static/images/dialog_header_25.png"></image>
		<image v-else class="xwwh-dialog-bg-header-26 animated  bounce"
			src="../../../static/images/dialog_header_26.png"></image>
		<!-- 恭喜获得 -->
		<image class="xwwh-dialog-tips-img animated rubberBand" src="../../../static/images/dialog_tips_img.png">
		</image>
		<!-- 主要内容 -->
		<view class="card-coupons animated bounceInRight">
			<!-- 25or26周年 -->
			<image v-if="prizeratetype !== 2" class="cc-left-icon" src="../../../static/images/mcb_no_converted25.png"></image>
			<image v-else class="cc-left-icon" src="../../../static/images/mcb_no_converted26.png"></image>
			<!-- 右边背景 -->
			<image class="cc-right-bg" src="../../../static/images/mcb_bg_white.png"></image>
			<view class="cc-right-info">
				<view class="cc-r-i-title">{{prizeratetype !== 2?'1元轻松享兑换劵':'1元享红牛兑换券'}}</view>
				<view class="cc-r-i-time">领取时间：{{time}}</view>
			</view>
		</view>
		<!-- 花边 -->
		<image class="xh-winning-window-lace animated bounceIn" src="../../../static/images/xh_winning_window.png">
		</image>
		<!-- 按钮部分 -->
		<view class="xwwh-xh-dialog-btn animated lightSpeedIn">
			<view class="xdb-item" @click="goCardBag">
				<view class="xdb-item-text deposit">存入卡包</view>
				<image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg02.png"></image>
			</view>
			<view class="xdb-item">
				<image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg01.png"></image>
				<button v-if="userInfo.mobile" class="xdb-item exchange" @click="exchange">马上兑换</button>
				<button v-else class="xdb-item exchange" open-type="getPhoneNumber"
					@getphonenumber="exchangeBefore">马上兑换</button>
			</view>
		</view>

		<!-- 关闭按钮 -->
		<image class="xwwh-xh-dialog-close fadeInOpacity" :style="{display: isShow?'block':'none'}"
			src="../../../static/images/toast_close.png" @click="close" />
		<!-- 黑幕 -->
		<view class="xh-dialog-black" @touchmove.stop></view>
	</view>

</template>

<script>
	import {
		mapGetters,
		mapMutations,
		mapActions
	} from 'vuex'
	import {parseTime} from '@/utils'
	import {setCheckCardVolume} from '@/utils/auth.js'
	export default {
		data() {
			return {
				parmas: null,//劵参数
				isShow:false,//显示隐藏控制
				time:parseTime(new Date()),
				prizeratetype:0//25 26 代表对应活动类型 25周年 26周年 
			}
		},
		computed: {
			...mapGetters(['userInfo'])
		},
		methods: {
			...mapMutations({
				setCheckCardVolume:'personal/setCheckCardVolume'
			}),
			...mapActions({
				updateUserMobile:'login/updateUserMobile',
				getUserInfo:'login/getUserInfo',
				getCardTopCount:'personal/getCardTopCount'
			}),
			show(data) {
				let {pid,pull_qr,prizeratetype} = data
				this.parmas = {pid,pull_qr}
				this.prizeratetype =prizeratetype
				this.isShow = true
				//中奖了更新顶部信息
				this.getCardTopCount()
			},
			close() {
				this.isShow = false
				this.$emit('closeNotice')
			},
			goCardBag() {
				this.$router.redirectTo({
					url: '/pages/personal/myCardBag/index'
				})
				this.isShow = false
			},
			exchangeBefore(e) {
					
					this.updateUserMobile(e).then(res => {
						//开始兑换
						this.exchange()
						//异步更新用户信息
						this.getUserInfo(true)
						
					})
				
			},
			exchange() {
				//深copy
				let parmas = JSON.parse(JSON.stringify(this.parmas))
				//本地缓存一份
				setCheckCardVolume([parmas])
				this.setCheckCardVolume([parmas]) //当前兑换卷
				this.$router.redirectTo({
					url: '/pages/personal/storesCode/index'
				})
				this.isShow = false
	
			}
		}
	}
</script>

<style lang="scss">
	.xh-winning-window-home {

		//背景
		.xwwh-dialog-bg {
			width: auto;
			height: 614rpx;
			position: fixed;
			left: 44rpx;
			right: 58rpx;
			z-index: 2;
			top: 150rpx;
		}

		// header
		.xwwh-dialog-bg-header-25,
		.xwwh-dialog-bg-header-26 {
			position: fixed;
			left: 50%;
			z-index: 4;
		}

		.xwwh-dialog-bg-header-26 {
			width: 534rpx;
			height: 268rpx;
			top: 176rpx;
			margin-left: -276rpx;
		}

		.xwwh-dialog-bg-header-25 {
			width: 516rpx;
			height: 266rpx;
			top: 194rpx;
			margin-left: -258rpx;
		}

		// 恭喜获得
		.xwwh-dialog-tips-img {
			width: 294rpx;
			height: 80rpx;
			position: fixed;
			top: 448rpx;
			left: 50%;
			margin-left: -147rpx;
			z-index: 3;
		}

		// 卡劵
		.card-coupons {
			width: 554rpx;
			height: 148rpx;
			position: fixed;
			top: 554rpx;
			left: 50%;
			margin-left: -269rpx;
			display: flex;
			align-items: center;
			border-radius: 5px;
			overflow: hidden;
			z-index: 3;
		}

		//按钮
		.xwwh-xh-dialog-btn {
			position: fixed;
			top: 750rpx;
			left: 50%;
			width: 623rpx;
			margin-left: -304rpx;
			padding-top: 20rpx;
			display: flex;
			justify-content: space-around;
			margin-top: 15rpx;
			z-index: 2;
		}

		.xwwh-xh-dialog-close {
			width: RPX(40);
			height: RPX(40);
			position: fixed;
			top: 900rpx;
			left: 50%;
			transform: translateX(-50%);
			z-index: 2;
		}

		// 花边
		.xh-winning-window-lace {
			position: fixed;
			z-index: 2;
			top: 510rpx;
			left: 20rpx;
			right: 20rpx;
			width: auto;
			height: 500rpx;
		}

		//动画出场顺序
		.xwwh-dialog-tips-img {
			-webkit-animation-delay: 0.5s;
			animation-delay: 0.5s;
		}

		.card-coupons,
		.xh-winning-window-lace {
			-webkit-animation-delay: 1s;
			animation-delay: 1s;
		}

		.xwwh-xh-dialog-btn {
			-webkit-animation-delay: 2s;
			animation-delay: 2s;
		}

		.cc-left-icon {
			width: 148rpx;
			height: 148rpx;
		}

		.cc-right-bg {
			position: absolute;
			z-index: -1;
			left: 148rpx;
			height: 148rpx;
			width: auto;
			right: 0;
			top: 0;
		}

		.cc-right-info {
			flex: 1;
			margin-left: 30rpx;
		}

		.cc-r-i-title {
			font-size: 30rpx;
			color: #333;
			font-weight: bold;
			margin-bottom: 15rpx;
		}

		.cc-r-i-time {
			font-size: 22rpx;
			color: #999;
		}

		.deposit {
			color: #F5231F;
		}

		.exchange {
			color: #614900;
		}

	}
</style>
